.timeline 
  width: 100%
  margin: 0 auto
  position: relative
  &-time 
    font-family: 'BScript_D', sans-serif
    font-weight: 400
    font-size: 48px


.timeline ul 
  padding: 0
  list-style: none
  


.timeline ul li 
  padding: 20px 0
  position: relative
  &:nth-child(odd)
      padding-right: 30%
  &:nth-child(even)
      padding-left: 30%


.heart-icon 
  width: 70px 
  height: 70px 
  position: absolute
  left: 50%
  bottom: 0
  transform: translateX(-50%)


.timeline ul li::after 
  content: ""
  position: absolute
  z-index: -2
  left: 50%
  bottom: 0
  transform: translate(-1px, 100%)
  width: 2px
  height: 100%
  background-color: #ccc


.timeline ul li:last-child::after 
  height: 0


.timeline .content 
  padding: 20px
  text-align: center


.timeline .content h3 
  margin: 0


.timeline .content p 
  margin-bottom: 0
