//     monospace
// 
@font-face 
    font-family: 'BScript_D'
    src: url(../../fonts/BScript_D.ttf)
    font-weight: 800

.wedding-invitation 
  display: flex
  justify-content: center
  align-items: center
  min-height: 100vh
  background: url(../../img/2408383.png) center cover repeat

  .container 
    text-align: center
  

  .title 
    font-size: 2rem
    margin-top: 2rem 
    margin-bottom: 1rem
    color: $primary-color
    font-family: "Cormorant", system-ui
    font-weight: 400
  

  .couple-info 
    font-family: 'BScript_D', sans-serif
    margin-bottom: 2rem

    .names 
      font-size: 5rem
      line-height: 1
      color: $primary-color

      .ampersand 
        font-size: 3rem
        margin: 0 0.5rem
      
    
  .couple-wrapper 
    display: flex
    padding: 10rem
    align-items: center
    gap: 30px

  .photo-container 
    width: 200px // Adjust photo size as needed
    height: 100%
    margin: 0 auto 2rem
    overflow: hidden
    border-radius: 10px
    img
      width: 100%
      height: 100%
      object-fit: cover
      border-radius: 20px
    
  

  .date 
    font-size: 1.5rem
    margin-bottom: 1rem
    color: $primary-color
  

  .play-music 
    background: none
    border: none
    padding: 0.5rem 1rem
    font-size: 1rem
    cursor: pointer
    color: $primary-color

    .play-icon 
      font-size: 1.5rem
      margin-right: 0.5rem
      border: solid 1px #704c33 
      padding-left: 6.47px
      padding-right: 2.47px
      border-radius: 100%
    
  
