.ceremony
    &-info 
        text-align: center
    &-details 
        font-size: 2rem
        margin-bottom: 10rem
        font-family: "Cormorant", system-ui
    &-subtitle 
        font-family: 'BScript_D', sans-serif
        font-size: 3rem
        margin-bottom: 2rem
        text-align: center
    &-calendar 
        position: relative
        max-width: 900px
        margin: 0 auto
        font-family: "Cormorant", system-ui
        font-size: 3rem
        display: flex
        gap: 10px
        flex-wrap: wrap
        text-align: left
        list-style-type: none
        margin-bottom: 50px
        justify-content: center
        &-item 
            width: 10% /* Подгоните по нужному размеру */
            display: flex
            justify-content: center
            align-items: center
            position: relative
            svg 
                width: 100%
                height: auto
                max-width: 68px
                transform: translateY(28%)
            p 
                font-size: 3rem 
                position: absolute
                z-index: 2 
                margin: 0
.content-wrapper 
    position: relative
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column


.title 
    font-size: 3rem
    margin-bottom: 3rem
    color: $primary-color
    font-family: "Cormorant", system-ui
    font-weight: 400
    text-align: center
