@media (max-width: 768px)
    .wedding-invitation .couple-wrapper
        padding: 40vw 0
        max-width: 100vw
    .wedding-invitation .couple-info .names
        font-size: 10vw
    .photo-container
        display: flex
        justify-content: center
        img
            max-width: 75vw
    .dresscode-photos img 
        max-height: 100%
        max-width: 45vw
    .form input[type=text]
        width: 80vw
    .location-link 
        width: 70%
    .timeline ul li:nth-child(even)
        padding-left: 50%
    .timeline ul li:nth-child(odd)
        padding-right: 50%

@media (max-width: 450px)
    .details 
        font-size: 1.5rem
        margin-top: 0
    .title 
        font-size: 2.5rem
    .subtitle
        font-size: 1.5rem   
    .wedding-invitation .title 
        font-size: 1.5rem
    .wedding-invitation .couple-wrapper
        margin: 0
        align-items: center
        padding: 50vw 0
    .wedding-invitation .container
        max-width: 100vw
    .wedding-invitation .photo-container img
        width: 90%
    .wedding-invitation .couple-info .names
        align-items: center
    .wedding-invitation .couple-info .names
        font-size: 10vw
    .location-link
        width: 35%
        font-size: 1rem
    .ceremony-calendar 
        max-width: 900px
        padding-right: 10px
        font-size: 1.5rem
        &-item 
            svg 
                top: 30%
                left: -37%
                max-width: 40px
                max-height: 25px
                transform: translateY(28%)

            p 
                font-size: 1.5rem
    .timeline ul li:nth-child(odd)
            padding-right: 50%
    .timeline ul li:nth-child(even)
            padding-left: 50%
        
    .heart-icon 
        width: 55px
        height: 50px
    .location-link 
        width: 70%
    .form input[type=text]
        width: 80vw
    .form button 
        width: 70%
        font-size: 1rem


       
