
.photo-container 
    max-width: 900px // Adjust photo size as needed
    height: 100%
    margin: 0 auto 2rem
    overflow: hidden
    border-radius: 10px
    img
      max-width: 100vw
      height: 100%
      object-fit: cover
      border-radius: 20px
.location-link
    display: block
    width: 18%
    margin: 0 auto 5rem 
    padding: 10px 10px
    color: #ffffff
    font-size: 23px
    font-family: "mama", Arial, sans-serif
    line-height: 1.55
    font-weight: 400
    border-radius: 100px
    background-color: #827561
    background-position: center center
    border-color: transparent
    border-style: solid
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out
    text-decoration: none
    text-align: center