.form 
  padding: 20px
  border-radius: 10px
  max-width: 900px
  margin: 0 auto
  text-align: center
  
  h2 
    margin-bottom: 10px
  

  label 
    display: block
    margin: 10px 0
  

  input[type="text"] 
    width: 100%
    padding: 10px
    margin: 10px 0
    color: #827561
  

  input[type="radio"] 
    margin-right: 10px
    color: #827561
  

  button 
    color: white
    padding: 10px 20px
    border: none
    cursor: pointer
    border-radius: 10px
    width: 18%
    margin: 0 auto 5rem
    padding: 10px 10px
    color: #ffffff
    font-size: 23px
    font-family: "mama", Arial, sans-serif
    line-height: 1.55
    font-weight: 400
    border-radius: 100px
    background-color: #827561
    background-position: center center
    border-color: transparent
    border-style: solid
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out
    text-decoration: none
    text-align: center
  

